import React, {Component} from 'react';
import {Label, Button} from 'semantic-ui-react'
import color from '../colors';
import {titleCase} from './Strings';

class AddItemBySearch extends Component {  
  render () {
    return (
      <React.Fragment>
        <Label basic size="big" pointing="right"
          style={{
            visibility:(this.props.showPopup)?'visible':'hidden',
            position:'fixed', top:'25%', right:'50%',
            boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
          }}
          content={"Select a category to add " + titleCase(this.props.itemName)}
        />
        { this.props.showButton ?
          <Button secondary size="massive" icon="add"
            // disabled={addSelectCat}
            style={{
              position:'fixed', left:'15%', top:'50%', zIndex:100000,
              boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
            }}
            onClick={this.props.onAdd}
          /> :
          null
        }
      </React.Fragment>
    )
  }
}

export default AddItemBySearch;


