import React, { Component } from 'react';
import { Button, List } from 'semantic-ui-react';
import PopupNotes from './PopupNotes';
import SidebarView from './SidebarView';

class Keypad extends Component {

  render() {
    
    const key = (label) => <Button className="ui black button" onClick={this.props.numberClicked} onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}>{label}</Button>
    const unit = (label) => <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>{label}</List.Item>

    function KeyRow(props) {
      return (
        <Button.Group size="big"  widths="3" style={{padding:'0px 5px'}} > 
          {props.children}
        </Button.Group>
      )
    }
    
    return (
      <SidebarView {...this.props}>
        { (this.props.item.notes.length > 0) ?
            <Button disabled={this.props.item.name.length === 0} icon="cancel" className="black" content="Delete note" style={{margin:5, marginTop:0}}
              onClick={this.props.deleteNote}
            />
          : 
          <PopupNotes
            item={this.props.item} 
            trigger={
              <Button disabled={this.props.item.name.length === 0} icon="edit" className="black" content="Add note" style={{margin:5, marginTop:0}}/>
            }
            {...this.props}
          />
        }
        <KeyRow>
          {key("")}{key("")}{key("")}
        </KeyRow>
        <KeyRow>
          {key(1)}{key(2)}{key(3)}
        </KeyRow>
        <KeyRow>
          {key(4)}{key(5)}{key(6)}
        </KeyRow>
        <KeyRow>
          {key(7)}{key(8)}{key(9)}
        </KeyRow>
        <KeyRow>
          <Button className="ui black button" icon="asterisk" onClick={this.props.asteriskClicked} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}/>
          {key(0)}
          <Button className="ui black button" icon="delete" onClick={this.props.deleteClicked} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}/>
        </KeyRow>
        <List size="small" selection celled inverted horizontal style={{margin:0, marginTop:5, marginBottom:15 }}>
          {unit('bags')}
          {unit('boxes')}
          {unit('trays')}
          {unit('tubs')}
          {unit('bin')}
          {unit('shelf')}
          {unit('pcs')}
        </List>
      </SidebarView>
    );
  }
}

export default Keypad;



