import React, {Component} from 'react';
import {Container, Popup, Button} from 'semantic-ui-react'

class DeletePopup extends Component {
    render () {
        return (
            <Popup basic hideOnScroll
                trigger={null}
                onClose={this.props.onClose} 
                open={this.props.open} 
                style={{
                position:'fixed',
                width:'80%',
                top:'40%',
                left:'10%',
                zIndex:10000
                }}
                >
                <Container textAlign="center">
                    <Popup.Header as="h3">
                        {"Delete " + this.props.item.name + "?"}
                    </Popup.Header>
                    <Popup.Content>
                        <Button negative
                            onClick={this.props.onDelete.bind(this,this.props.item)}
                        >
                            {/* <Icon name="exclamation"/> */}
                            YES, delete it!
                        </Button>
                    </Popup.Content>
                </Container>
            </Popup>
        )
    }
}

export default DeletePopup;


